@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .reasons {
        &_item {
            align-items: flex-start; }
        &_descr {
            width: 300px; } }
    .mobile {
        &_item {
            width: 300px; } } }


@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 5px; }
        &_link {
            font-size: 11px; } }
    .subheader {
        &_logo {
            width: 100%; }
        &_official {
            font-size: 12px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            margin-top: 21px;
            height: 40px;
            width: 100%; } }
    .promo {
        padding: 135px 0 100px;
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .reasons {
        &_header {
            font-size: 30px; }
        &_item {
            flex-direction: column;
            align-items: center; }
        &_round {
            margin: 0; }
        &_descr {
            width: 300px;
            margin-top: 20px;
            text-align: center; } }
    .mobile {
        min-height: 800px;
        &_header {
            font-size: 30px; }
        &_item {
            width: 220px;
            height: 220px;
            padding-top: 75px;
            &_subtitle {
                font-size: 15px;
                line-height: 20px; } } }
    .choice {
        padding-bottom: 100px;
        min-height: 800px;
        &_title {
            font-size: 30px; }
        &_img {
            width: 100%; } }
    .require {
        &_title {
            font-size: 30px; }
        &_subtitle {
            font-size: 20px; } }
    .map {
        &_title {
            font-size: 30px; } } }

