* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative;
  height: 50px; }

nav {
  background: #212121;
  position: fixed;
  width: 100%;
  z-index: 2; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;
  &_link {
    font-weight: 300;
    font-size: 15px;
    color: #FFF;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
      color: #65DEF4;
      transition: all 0.3s ease-out; } }
  &_item {
    border-right: 2px dotted #000;
    padding-right: 28px;
    &:last-child {
      border-right: none; } } }

.subheader {
  height: 84px;
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 50px;
  width: 100%;
  &_link {
    display: block;
    margin-top: 16px;
    max-width: 100%; }
  &_official {
    margin-top: 6px;
    font-size: 13px;
    color: #FFF;
    opacity: 0.5; }
  &_call {
    margin-top: 19px;
    font-weight: 300;
    font-size: 12px;
    text-align: right;
    color: #FFF;
    opacity: 0.8; }
  &_phone {
    display: block;
    margin-top: 4px;
    font-size: 22px;
    line-height: 23px;
    text-transform: uppercase;
    color: #FFF;
    text-align: right;
    text-decoration: none;
    &:hover {
      color: #65DEF4;
      transition: all 0.3s ease-out; } }
  &_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    font-size: 14px;
    text-transform: uppercase;
    color: #FFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    &:hover {
      background-image: none;
      background-color: #1CA8C3; } } }

.promo {
  min-height: 800px;
  background: url('../img/1_section/bg-01.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px;
  &_header {
    font-size: 60px;
    line-height: 60px;
    font-weight: 700;
    color: #FFF;
    text-align: center;
    text-transform: uppercase; }
  &_subheader {
    margin-top: 22px;
    font-size: 36px;
    line-height: 36px;
    font-weight: 700;
    color: #FFF;
    text-align: center;
    text-transform: uppercase; }
  &_descr {
    margin-top: 23px;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #FFF; }
  &_btn {
    display: block;
    margin: 118px auto 0;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    font-size: 18px;
    color: #FFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    &:hover {
      background-image: none;
      background-color: #1CA8C3; } } }

.reasons {
  min-height: 850px;
  background-color: #FFF;
  padding: 77px 0 67px;
  &_header {
    margin-top: 33px;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #222; }
  &_item {
    margin-top: 26px;
    display: flex;
    justify-content: center;
    align-items: center; }
  &_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-right: 26px;
    width: 116px;
    height: 116px;
    background-color: #1EACC7;
    border-radius: 100%; }
  &_descr {
    width: 372px;
    font-size: 15px;
    line-height: 21px;
    color: #202020;
    span {
      font-size: 17px;
      color: #1EACC7; } } }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000;
  color: #fff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center; }

.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/3_section/bg.jpg') center (center / cover) no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url('../img/3_section/img-01.jpg') center (center / cover) no-repeat; }
    &_2 {
      background: url('../img/3_section/img-02.jpg') center (center / cover) no-repeat; }
    &_3 {
      background: url('../img/3_section/img-03.jpg') center (center / cover) no-repeat; }
    &_4 {
      background: url('../img/3_section/img-04.jpg') center (center / cover) no-repeat; }
    &_5 {
      background: url('../img/3_section/img-05.jpg') center (center / cover) no-repeat; }
    &_6 {
      background: url('../img/3_section/img-06.jpg') center (center / cover) no-repeat; }
    &_subtitle {
      color: #ffffff;
      font-size: 24px;
      font-weight: 400;
      line-height: 31px;
      text-transform: uppercase;
      text-align: center; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      width: 26px;
      height: 26px;
      background-color: #000000;
      margin: 0 auto;
      border-radius: 100%;
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: #1eacc7;
        transition: 0.3s all; }
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        height: 14px;
        width: 2px;
        background-color: #1eacc7;
        transition: 0.3s all; }
      &:hover {
        &:before, &:after {
          background-color: #FFF; } } } } }

.mobile_header {
  margin-top: 33px;
  font-weight: 700;
  font-size: 38px;
  text-align: center;
  text-transform: uppercase;
  color: #222; }

.mobile_subtitle {
  margin-top: 19px;
  color: #222;
  font-size: 17px;
  font-weight: 400;
  text-align: center; }

.choice {
  position: relative;
  padding: 54px 0 137px;
  min-height: 865px;
  background: url('../img/4_section/bg.jpg') center (center / cover) no-repeat;
  &_label {
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #fff;
    color: #000;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }
  &_title {
    margin-top: 27px;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #fff; }
  &_subtitle {
    font-size: 17px;
    text-align: center;
    color: #fff; }
  &_img {
    display: block;
    margin: 56px auto 0 auto; }
  &_descr {
    text-align: center;
    color: #222222;
    font-size: 17px;
    font-weight: 300;
    line-height: 24px; } }

.require {
  padding: 68px 0 51px;
  &_title {
    margin-top: 27px;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #222; }
  &_block {
    margin: 33px 0; }
  &_subtitle {
    font-size: 22px;
    color: #1CA8C3; }
  &_list {
    margin-top: 22px;
    position: relative;
    li {
      margin-bottom: 10px;
      list-style-type: none;
      font-weight: 300;
      font-size: 17px;
      line-height: 24px;
      color: #222;
      &::before {
        content: '';
        display: block;
        position: absolute;
        left: -5px;
        width: 21px;
        height: 20px;
        background: url('../img/4_section/ico-01.png') center (center / cover) no-repeat; } } }
  &_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    color: #1CA8C3; }
  .red {
    .require_subtitle {
      color: #FF5656; }
    li::before {
      background: url('../img/4_section/ico-02.png') center (center / cover) no-repeat; } } }

.map {
  min-height: 720px;
  background: url('../img/5_section/bg.jpg') center (center / cover) no-repeat;
  padding: 50px 0;
  &_label {
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #fff;
    color: #000;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }
  &_title {
    margin-top: 27px;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #fff; }
  &_subtitle {
    font-size: 17px;
    text-align: center;
    color: #fff; }
  &_link {
    margin-top: 456px;
    font-size: 15px;
    text-align: center;
    text-transform: uppercase;
    text-decoration: underline;
    color: #1FBAD6;
    a {
      margin-top: 456px;
      font-size: 15px;
      text-align: center;
      text-transform: uppercase;
      text-decoration: underline;
      color: #1FBAD6;
      &:hover {
        color: #65DEF4;
        transition: all 0.3s ease-out; } } } }

footer {
  padding: 14px 23px 39px 23px;
  a {
    text-decoration: none; } }

.footer {
  &_divider {
    width: 299px;
    height: 4px;
    background-color: #1fbad6;
    margin: 0 auto; }
  &_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 32px; }
  &_wrapper > div {
    width: 33%;
    &:nth-child(3) {
      display: flex;
      justify-content: flex-end; } }
  &_social {
    width: 160px;
    display: flex;
    justify-content: space-between;
    &_item {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 4px;
      width: 32px;
      height: 32px;
      background-color: #070716;
      border-radius: 100%;
      i {
        color: #fff; } } }
  &_links {
    width: 390px;
    margin: 0 auto;
    &_main {
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      a {
        color: #070716;
        font-size: 15px;
        font-weight: 300;
        text-transform: uppercase; } }
    &_sub {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      a {
        color: #999999;
        font-size: 11px;
        font-weight: 300;
        text-transform: uppercase; } }
    &_lang {
      a {
        display: block;
        margin: 0 auto;
        color: #1fbad6;
        font-size: 11px;
        font-weight: 400;
        margin-top: 19px;
        text-transform: uppercase;
        text-align: center;
        &:hover {
          color: #65DEF4;
          text-decoration: underline;
          transition: all 0.3s ease-out; } } } } }

@import "media";
